:root {
  --gutter: 15px;
  --mob-padding: 20px;
}

.container {
  max-width: var(--content-width);
  padding-left: var(--mob-padding);
  padding-right: var(--mob-padding);
  margin-left: auto;
  margin-right: auto;
}

.row {
  display: flex;
  flex-wrap: wrap;
  margin-left: calc(var(--gutter) * -1);
  margin-right: calc(var(--gutter) * -1);
}

.btn {
  color: var(--main-text-color);
  border-radius: 50px;
  padding: 10px 30px;
  font-size: 16px;
  line-height: 150%;
}

.footer {
  background: red;
  font-size: 24px;
  color: grey;
  border: 1px;
  border-color: red;
}

.header {
  padding: 38px 80px;
  display: flex;
  align-items: center;
  color: var(--main-text-color);
  justify-content: space-between;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 4;
}

.header_has-bg {
  background-color: var(--bg-primary);
  color: var(--txt-black);
  transition: 0.04s;
}

.header_has-bg .header__burger-img path {
  fill: var(--bg-secondary);
}

.header__menu-wrap {
  padding-left: 300px;
}

.header__menu {
  display: flex;
  padding: 0;
  margin: 0;
}

.header .menu-item {
  padding: 15px 28px 15px 28px;
  color: currentColor;
  list-style: none;
  font-size: 16px;
  position: relative;
}

.header .menu-item:hover {
  background: var(--bg-primary);
}

.header .menu-item:hover>a {
  color: var(--txt-black);
}

.header .menu-item>a {
  text-decoration: none;
  color: currentColor;
  position: relative;
}

.header__menu-link {
  text-decoration: none;
  color: currentColor;
}

.header .menu-item-has-children::after {
  content: "";
  display: inline-block;
  width: 6px;
  height: 6px;
  border-left: 1px solid currentColor;
  border-bottom: 1px solid currentColor;
  margin-left: 5px;
  transform: translateY(-1px) rotate(-45deg);
  vertical-align: middle;
}

.header .menu-item-has-children:hover::after {
  content: "";
  transform: translateY(-1px) rotate(180deg);
  border-left: 1px solid var(--txt-black);
  border-bottom: 1px solid var(--txt-black);
  transition: 0.04s;
}

.header .menu-item-has-children:hover::after {
  content: "";
  transform: translateY(-1px) rotate(135deg);
  border-left: 1px solid var(--txt-black);
  border-bottom: 1px solid var(--txt-black);
  transition: 0.04s;
}

.header .menu-item-has-children:hover .sub-menu {
  opacity: 1;
  visibility: visible;
  transform: translate(0, 0);
}

.header .menu-item-has-children:hover .menu-item {
  padding-left: 5px;
  padding-right: 0;
}

.header .menu-item-has-children:hover .menu-item:hover {
  background: var(--txt-black);
}

.header .menu-item-has-children:hover .menu-item:hover>a {
  height: 100%;
  background-color: var(--txt-black);
  color: var(--main-text-color);
}

.header .sub-menu {
  position: absolute;
  top: 100%;
  left: 0;
  width: 150px;
  background: var(--bg-primary);
  opacity: 0;
  visibility: hidden;
  transform: translate(0, -20px);
  transition: .3s;
  padding-left: 0;
  color: var(--txt-black);
}

.header__login-btn {
  background: linear-gradient(94.26deg, #C89AFC 9.51%, #7C6AFA 90.23%);
  border: none;
}

.header__login-btn:hover {
  background: green;
  transition: 0.6s;
}

.header__burger {
  display: none;
}

@media (max-width: 1600px) {
  .header__menu-wrap {
    padding-left: 200px;
  }
}

@media (max-width: 1400px) {
  .header__menu-wrap {
    padding-left: 0;
  }
}

@media (max-width: 1200px) {
  .header {
    padding: 20px 30px;
  }

  .header_has-bg .header__login-btn {
    background: var(--bg-secondary);
    color: var(--main-text-color);
    border-color: var(--main-text-color);
    font-weight: bold;
  }

  .header_has-bg .header__login-btn:hover {
    background: green;
    transition: 0.6s;
  }

  .header__menu-wrap {
    display: none;
  }

  .header__menu {
    flex-direction: column;
    align-items: center;
    background-color: var(--bg-primary);
  }

  .header__menu-scrolled {
    background-color: var(--bg-primary);
    transition: 0.4s;
  }

  .header__menu-wrap_visible {
    display: block;
    position: absolute;
    top: 100%;
    left: 0%;
    background-color: var(--bg-primary);
    width: 100%;
    transition: 0.4s;
    color: var(--txt-black);
  }

  .header .menu-item:hover {
    background: var(--bg-secondary);
  }

  .header .menu-item:hover>a {
    color: var(--main-text-color);
  }

  .header .menu-item {
    padding: 15px 28px;
  }

  .header .menu-item-has-children:hover::after {
    border-left: 1px solid var(--main-text-color);
    border-bottom: 1px solid var(--main-text-color);
  }

  .header .menu-item-has-children:hover .menu-item:hover>a {
    color: var(--main-text-color);
    background-color: var(--bg-secondary);
  }

  .header .sub-menu {
    top: 0;
    left: 100%;
    background-color: var(--bg-primary);
    flex-direction: row;
  }

  .header__login-btn {
    background: var(--bg-primary);
    color: var(--txt-black);
    border-color: var(--txt-black);
    font-weight: bold;
  }

  .header__burger {
    display: block;
  }
}